import Header from "@/components/header"
import Footer from "@/components/footer"
import { getMarkdownByPath } from "@/lib/markdown"
import MarkdownContent from "@/components/markdown-content"
import TableOfContents from "@/components/table-of-contents"
import PageHeroSection from "@/components/page-hero-section"
import SidebarQuoteForm from "@/components/sidebar-quote-form"
import { pageConfigs } from "@/lib/page-configs"

export default function SurfaceFinishBAPage() {
  let markdownContent = ""
  try {
    const { content } = getMarkdownByPath('app/surface finish/ba.md')
    markdownContent = content
  } catch (error) {
    markdownContent = ""
  }
  
  const config = pageConfigs['surface-finish/ba']
  
  return (
    <>
      <Header />
      <PageHeroSection
        title={config.title}
        description={config.description}
        backgroundImage={config.backgroundImage}
      />
      <div className="max-w-7xl mx-auto px-4 py-12">
        <div className="grid lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            {markdownContent ? (
              <>
                <TableOfContents content={markdownContent} />
                <MarkdownContent content={markdownContent} />
              </>
            ) : (
              <div className="text-center text-gray-500 py-12">
                <p>Content will be displayed here once the markdown file is added.</p>
              </div>
            )}
          </div>
          <div className="lg:col-span-1">
            <SidebarQuoteForm />
          </div>
        </div>
      </div>
      <Footer />
    </>
  )
}

